import Swiper from 'swiper';
import 'swiper/css';

function Carousel(el) {
    const carousel = el.querySelector('.temoignages-carousel');
    const btn_prev = el.querySelector('.btn-prev');
    const btn_next = el.querySelector('.btn-next');

    const swiper = new Swiper(carousel, {
        speed: 400,
        slidesPerView: 1,
        spaceBetween: 10,
        loop: false,
        centeredSlides: false,
        centeredSlidesBounds: false,
        grabCursor: true,
        breakpoints: {
            576: {
                spaceBetween: 10,
                slidesPerView: 1,
            },
            768: {
                spaceBetween: 10,
                slidesPerView: 1,
            },
            992: {
                spaceBetween: 10,
                slidesPerView: 'auto',
            },
            1200: {
                spaceBetween: 10,
                slidesPerView: 'auto',
            },
            1370: {
                spaceBetween: 10,
                slidesPerView: 'auto',
            },
        },
        navigation: {
            nextEl: '.btn-prev',
            prevEl: '.btn-next',
        },
    });

    const next = () => {
        swiper.slideNext();
    };

    const prev = () => {
        swiper.slidePrev();
    };

    //btn_prev.addEventListener('click', prev);
    //btn_next.addEventListener('click', next);
}

export default Carousel;